<template>
  <div id="main">
    <header class="page-header" role="banner">
      <h1 class="project-name">Joker</h1>
      <h2 class="project-tagline">一个快速架构微服务的服务容器</h2>
        <a @click="$router.push('/use')" class="btn">开始使用</a>
      
        <a href="https://github.com/jsen-joker/Joker" class="btn space">Github</a>
        <a href="https://github.com/jsen-joker/Joker/zipball/master" class="btn">下载 .zip</a>
        <a href="https://github.com/jsen-joker/Joker/tarball/master" class="btn">下载 .tar.gz</a>
    </header>
    <div class="content">
      <p class="start">快速开始</p>
      <div class="page-home-start" style="width: 650px;"> 
        <code> 
          <div class="page-home-comment">// 下载</div> 
          <div> 
            <div class="pl-smi">git clone https://github.com/jsen-joker/Joker.git</div> 
            <div class="pl-smi">cd Joker/joker/bin</div> 
          </div> 
          <div class="page-home-comment">// 安装依赖（基于mvn）</div> 
          <div> 
            <div class="pl-smi">./joker.sh install</div> 
          </div>
          <div class="page-home-comment">// 运行、停止</div> 
          <div> 
            <div class="pl-smi">./startup.sh</div> 
            <div class="pl-smi">./shutdown.sh</div> 
            <div class="pl-smi">./joker.sh start / stop</div> 
          </div> 
        </code> 
      </div>
      <p class="start small">特性介绍</p>
      <div class="group">
        <div class="item">
          <img src="../assets/light.svg" />
          <div>
            <span>轻量</span> joker基于vertx开发，vertx本身是一套开发微服务的技术栈，相比同类产品，joker是一个更为敏捷的开发选择。
          </div>
        </div>
        <div class="item">
          <img src="../assets/quick.svg" />
          <div>
            <span>快速</span> joker基于netty内核运行，在http等网络协议方面，拥有更高的运行速度，同时基于netty的异步开发模式，让开发更加畅快。
          </div>
        </div>
        <div class="item">
          <img src="../assets/eff.svg" />
          <div>
            <span>高效</span> 纯异步开发模式，模块化，细粒度的服务开发极大的提高了开发人员的效率。
          </div>
        </div>
        <div class="item">
          <img src="../assets/easy.svg" />
          <div>
            <span>一键部署</span> joker提供了一些核心插件，让你可以快速部署一个微服务模块，不用重启服务器，拖拽即可运行。
          </div>
        </div>
      </div>
    </div>
    <footer class="page-home-footer"> <a href="https://github.com/jsen-joker/Joker">© Joker</a> </footer>
  </div>
</template>

<script>
export default {
  name: 'index'
}
</script>

<style lang="scss" scoped>
#main {
  .page-home-footer {
    margin: 60px 0 20px;
    text-align: center;
    a {
      color: #26a2ff;
    }
  }
  .content {
    .start {
      text-align: center;
      color: #147879;
      font-size: 2rem;
    }
    .small {
      font-size: 1.6rem;
    }
    .group {
      margin: 40px auto 60px;
      width: 720px;
      .item {
        color: #333;
        margin-bottom: 50px;
        display: flex;
        img {
          width: 64px;
          height: 64px;
        }
        >div {
          margin-left: 10px;
          margin-right: 20px;
          span {
            padding-left: 20px;
            font-weight: bolder;
            line-height: 1.6;
            vertical-align: middle;
            font-size: 1.4rem;
          }
        }
      }
    }
    .page-home-start {
      margin: 40px auto 60px;
      background-color: #f8f8f8;
      border-radius: 8px;
      border: 1px solid #ddd;
      padding: 15px;
      code {
        display: block;
        margin-top: 25px;
        div.page-home-comment {
          color: #969896;
        }
        >div {
          display: block !important;
          line-height: 20px;
        }
        .pl-smi {
          color: #333;
        }
      }
      :first-child {
        margin-top: 0;
      }
    }
  }
  .page-header {
    padding: 5rem 6rem;
    color: #fff;
    text-align: center;
    background-color: #159957;
    background-image: linear-gradient(120deg, #155799, #159957);
    .project-name {
      font-size: 3.25rem;
      margin-top: 0;
      margin-bottom: 0.1rem;
    }
    .project-tagline {
      font-size: 1.25rem;
      margin-bottom: 2rem;
      font-weight: normal;
      opacity: 0.7;
    }
    .btn {
      display: inline-block;
      margin-bottom: 1rem;
      color: rgba(255,255,255,0.7);
      background-color: rgba(255,255,255,0.08);
      border-color: rgba(255,255,255,0.2);
      border-style: solid;
      border-width: 1px;
      border-radius: 0.3rem;
      transition: color 0.2s, background-color 0.2s, border-color 0.2s;
      padding: 0.75rem 1rem;
    }
    .btn:hover {
      color: rgba(255,255,255,0.8);
      text-decoration: none;
      background-color: rgba(255,255,255,0.2);
      border-color: rgba(255,255,255,0.3);
    }
    .space {
      margin-left: 2rem;
    }
  }
}
</style>



